import React, {Component} from "react";
import FixTvFilteredView from "eshop/modules/fix/components/FixTvFilteredView"
import OraModal from "eshop/modules/core/components/ui/Modal"
import {connect} from "react-redux";
import {getTvFilteredModalVisibility} from "eshop/modules/cart/selectors"
import {getTvPackages} from "eshop/modules/cart/selectors"
import {changeFixTvFilteredModalVisibility} from "eshop/modules/cart/actions/cart";
import {isPreLandingPage} from "eshop/modules/fix/selectors/root";
class FixTvFilteredModal extends React.Component{


    render(){
        return (
        <OraModal open={this.props.tvFilteredModalVisibility} onClose={this.props.onClose}>
            <FixTvFilteredView isPreLandingPage={this.props.isPreLandingPage} tvPackages={this.props.tvPackages}></FixTvFilteredView>
        </OraModal>
        )
    }
}



const mapStateToProps = (state) => ({
    tvFilteredModalVisibility: getTvFilteredModalVisibility(state),
    tvPackages:getTvPackages(state),
    isPreLandingPage:isPreLandingPage(state)
});

const mapDispatchToProps = dispatch => ({
    onClose : () => dispatch(changeFixTvFilteredModalVisibility(false))

});
export default connect(mapStateToProps, mapDispatchToProps)(FixTvFilteredModal);